import { tr } from "@/services/lang";
export default {
  props: {
    title: {
      type: String,
      default: "",
    },
    image: {
      type: String,
      default: "",
    },
    price: {
      type:String,
      default: 0,
    },
    id:{
      type: String,
      default: 0,
    },
    mt: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    lang() {
      return tr();
    },
  },
  data() {
    return {
      is_hover: false,
    };
  },
  methods: {
    item_hover() {
      if (window.innerWidth > 768) {
        this.is_hover = true;
      }
    },
    add_item(){
        this.$store.dispatch("products/addProductToCart",{id:this.id,name:this.title,price:this.price,img:this.image})
    }
  },
};
